import * as React from "react";
import * as PropTypes from "prop-types";

import NamespaceWrapper, { NamespaceWrapperProps } from "../utils/NamespaceWrapper";

export interface MetadataProps extends NamespaceWrapperProps {
    label ?: React.ReactNode;

    className ?: string;

    level ?: "info" | "warning" | "error" | "success";

    size ?: "xsmall" | "small" | "medium" | "large" | "xlarge";

    position ?: "left" | "center" | "right";
}

export default class Metadata extends NamespaceWrapper<MetadataProps, {}> {
    public static displayName = "Metadata";

    public static propTypes = {//
        "label": PropTypes.node, //

        "className": PropTypes.string, //
        "level": PropTypes.oneOf(["info", "warning", "error", "success"]), //
        "size": PropTypes.oneOf(["xsmall", "small", "medium", "large", "xlarge"]), //
        "position": PropTypes.oneOf(["left", "center", "right"])//
    };

    public render() {
        const className = this.classes(
          "Metadata",
          this.props.size && "Metadata--" + this.props.size,
          this.props.level && "Metadata--" + this.props.level,
          this.props.position && "Metadata--" + this.props.position,
          this.props.className
        );

        return <div className={className}>
            <div className={this.classes("Metadata__value")}>{this.props.children}</div>
            <div className={this.classes("Metadata__label")}>{this.props.label}</div>
        </div>;
    }
}
